import React from "react";
import { Button, message, Select } from "antd";
import "../styles/css/video-filter.scss";
const { Option } = Select;
interface IProps {}
interface IState {
  constraints: {
    audio: boolean | true;
    video: boolean | true;
  };
  // // 
  myVideoRef: React.RefObject<HTMLVideoElement>;
  // 
  myVideoRef2: React.RefObject<HTMLVideoElement>;
  // 
  myAudioRef: React.RefObject<HTMLAudioElement>;
  // 
  myCanvas: React.RefObject<HTMLCanvasElement>;
}

class TurnCamera extends React.Component<IProps, IState> {
  constructor(porps: any) {
    super(porps);
    this.state = {
      // 
      constraints: {
        audio: true,
        video: true,
      },
      // 
      myVideoRef: React.createRef(),
      // 
      myVideoRef2: React.createRef(),
      // 
      myAudioRef: React.createRef(),
      // 
      myCanvas: React.createRef(),
    };
  }
  render() {
    return (
      <div className="container">
        <h1>
          <span></span>
        </h1>
        <video
          className="video"
          ref={this.state.myVideoRef}
          autoPlay
          playsInline
        />
        <audio ref={this.state.myAudioRef} autoPlay />
        <Button className="button" onClick={this.openCamera}>
          
        </Button>
        <Select
          defaultValue="video"
          style={{ width: "100px" }}
          onChange={this.filterChange}
        >
          <Option value="video"></Option>
          <Option value="blur"></Option>
          <Option value="grayscale"></Option>
          <Option value="invert"></Option>
          <Option value="sepia"></Option>
        </Select>
        <video
          className="video"
          ref={this.state.myVideoRef2}
          autoPlay
          playsInline
        />
        <Button className="button" onClick={this.shareScreen}>
          
        </Button>
        <canvas className="canvas" ref={this.state.myCanvas} />
        <Button className="button" onClick={this.takeSnap}>
          
        </Button>
      </div>
    );
  }
  /**
   * @description: 
   */
  openCamera = () => {
    navigator.mediaDevices
      .getUserMedia(this.state.constraints) //
      .then(this.handleSuccess)
      .catch(this.handleError);
  };
  /**
   * @description: 
   * @param {*}
   * @return {*}
   */
  handleSuccess = (stream: MediaStream): any => {
    const video = this.state.myVideoRef.current;
    const audio = this.state.myAudioRef.current;
    if (video !== null && audio !== null) {
      video.srcObject = stream;
      audio.srcObject = stream;
    } else {
      return;
    }
  };
  /**
   * @description: 
   */
  handleError = (error: any) => {
    let str = "" as string;
    if (error.name === "NotFoundError") {
      str = "()";
    } else {
      str = "";
    }
    message.error(`getUserMedia:${error.name}:${error.message}-${str}`, 2);
  };
  /**
   * @description: 
   */
  takeSnap = () => {
    let video = this.state.myVideoRef.current;
    let canvas = this.state.myCanvas.current;
    if (video !== null && canvas !== null) {
      canvas.width = video.videoWidth;
      canvas.height = video.videoHeight;
      const context = canvas.getContext("2d");
      if (context !== null) {
        context.drawImage(video, 0, 0, canvas.width, canvas.height);
      } else {
        return;
      }
    } else {
      return;
    }
  };
  /**
   * @description: 
   */
  shareScreen = () => {
    navigator.mediaDevices
      .getDisplayMedia({ video: true })
      .then(this.shareScreenHandleSuccess)
      .catch(this.shareScreenHandleError);
  };
  /**
   * @description: 
   * @param {JSON} stream 
   */
  shareScreenHandleSuccess = (stream: MediaStream) => {
    const video = this.state.myVideoRef2.current;
    if (video !== null) {
      video.srcObject = stream;
    } else {
      return;
    }
  };
  /**
   * @description: 
   * @param {JSON} stream 
   */
  shareScreenHandleError = (error: any) => {
    let str;
    if (error.name === "NotFoundError") {
      str = "()";
    } else {
      str = "";
    }
    message.error(`getUserMedia:${error.name}:${error.message}-${str}`, 2);
  };
  /**
   * @description: 
   */
  filterChange = (value: string) => {
    const video = this.state.myVideoRef.current;
    if (video !== null) {
      video.className = value;
    } else { 
      return;
    }
  };
}
export default TurnCamera;
